import { Typography, Button as AButton } from "antd";
import { useDispatch, useSelector } from "react-redux";
import React, { useEffect, useState, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import { setAppLoader } from "../../../redux/common/CommonActions";
import {
  getEmailMask,
  getErrorMessageFromAPI,
  setUserLocalStorage,
  triggerNotification,
  uniqueID,
} from "../../../utilz/utilz";
import OtpInput from "react-otp-input";
import { verifyAuthenticationTokenEffect } from "../../../redux/auth/AuthEffects";
import { NOTIFICATION_TYPES } from "../../../contants/common";
import { setLoginSuccess } from "../../../redux/auth/AuthActions";
import { cloneDeep, omit } from "lodash";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const AuthVerifyTokenForm = () => {
  const [otp, setOtp] = useState("");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const [loading, setLoading] = useState(false);
  const { notificationAPI } = useSelector((state) => state.common);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const onSubmit = async () => {
    try {
      setLoading(true);
      await verifyAuthenticationTokenEffect({
        email: user?.data?.email,
        token: otp,
      });
      setLoading(false);
      let userData = cloneDeep(user?.data);
      userData = omit(userData, ["twoFactorEnabled"]);
      setUserLocalStorage(userData);
      dispatch(setLoginSuccess({ data: userData }));
      dispatch(setAppLoader(true));
      navigate(AUTH_ROUTES.LOGIN, { replace: true });
    } catch (error) {
      const message = getErrorMessageFromAPI(error);
      setLoading(false);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: message ? message : t('translation:verifyToken.notification'),
      });
    }
  };

  useEffect(() => {
    if (!user?.data?.email) {
      navigate(AUTH_ROUTES.LOGIN, { replace: true });
    }
  }, [user?.data?.email]);

  return (
    <div className="mt-20">
      <p className="f-18 fw-400 mb-5">
        {t('translation:verifyToken.enter')}{" "}
        {getEmailMask(user?.data?.email)}
      </p>

      <Typography.Text id={uniqueID()} type="danger" className="f-13">
        {user?.error ? user.message : ""}
      </Typography.Text>
      <div className="w-100 pb-4 pt-10 px-50">
        <OtpInput
          containerStyle={"otp-container"}
          value={otp}
          onChange={(otp) => setOtp(otp)}
          numInputs={6}
          placeholder={"000000"}
          isInputNum={true}
        />
      </div>
      <AButton
        disabled={otp?.length !== 6}
        type="primary"
        id={uniqueID()}
        loading={loading}
        htmlType="button"
        onClick={onSubmit}
        className="background-header mt-30 w-100 br-4"
      >
        <span className="auth-button-labels mt-n2">
          {t("translation:verifyToken.verify")}
        </span>
      </AButton>
      <div className="text-center mt-7">
        <Link
          id={uniqueID()}
          to={AUTH_ROUTES.USER_SEND_AUTHENTICATION_TOKEN}
          type="link"
          size={"small"}
          className="forgot-password-text fw-600 "
        >
          {t("translation:verifyToken.receive")}
        </Link>
      </div>
    </div>
  );
};

export default AuthVerifyTokenForm;
